.table-container{padding:0}.table{width:100%;margin:2rem 0;text-align:left;border-collapse:collapse}.table th{padding:1rem 0;font-size:1.2rem;font-weight:400;cursor:pointer}.table .orderable::after{font-family:bootstrap-icons;content:"";position:relative;left:.5rem;top:.2rem;font-size:1rem}.table .orderable.active-asc::after{content:""}.table .orderable.active-desc::after{content:""}.table td{border-top:2px solid #252422;padding:1rem 0}.table .index-field{padding:1rem .5rem;text-align:center}.form label{display:block;padding:.5rem;font-weight:500}.form input{padding:.5rem;border-radius:5px;font-size:1rem}@media(width <= 64rem){.table-container{padding:0 1rem}}