.dialog{top:50%;left:50%;transform:translate(-50%, -50%);background-color:rgba(0,0,0,0);border:none;width:fit-content;height:fit-content}.dialog::backdrop{background-image:linear-gradient(180deg, #969696, #252422);opacity:.4}.modal-container{min-width:24rem;background-color:#eff2ec;padding:0 .5rem;border-radius:.5rem;border:2px solid #969696}.header{padding:1rem;border-bottom:1px solid #969696;display:flex;justify-content:space-between}.header h1{font-size:1.5rem;font-weight:400}.header .cancel-button{border:none;background-color:rgba(0,0,0,0)}.header .cancel-button::after{font-family:bootstrap-icons;content:"";font-size:1.5rem;font-weight:600}.content{padding:1rem}.footer{padding:1rem;border-top:1px solid #969696;text-align:end}.footer button{padding:.5rem 2rem;text-align:center;background-color:#eff2ec;border-radius:5px}